  .categories__item:nth-child(10n) {
    grid-column: auto/span 12;
  }

  .categories__item:nth-child(2),
  .categories__item:nth-child(10n) {
    grid-row: 3/4;
  }
}

@media (any-hover: hover) {
  .category:hover {
    border-radius: 12px;
  }

  .category:hover .category__image img {
    transform: scale(1.1);
  }

  .category:hover .category__text-hover {
    animation: showText 0.7s forwards;
  }

  .category:hover .category__text-hover._medium {
    height: 120px;
  }

  .category:hover .category__text-hover._large {
    height: 150px;
  }

  .category:hover .category__label {
    transform: translate(0px, -8px);
  }

  .product-hover {
    padding-top: 250px;
    transition: padding-top 0.3s ease 0s;
  }

  .product-hover:hover {
    padding-top: 70px;
  }

  .product-hover:hover .text-hover-product__label,
  .product-hover:hover .text-hover-product__articol {
    animation: showText 0.7s forwards;
    height: auto;
  }

  .product__main {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex: 1 1 auto;
    transition: flex-grow 0.3s ease 0.3s;
  }

  .product__bottom {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .product__image-wrapper_hover {
    position: absolute;
    top: 0;
    width: 100%;
    height: auto;
    z-index: 1;
    transition: top 0.5s ease 0s;
  }

  .product__image-wrapper_hover._hide-image {
    top: -300px;
  }

  .product__btn:hover {
    background-color: var(--light-gray);
  }

  .product__btn:hover svg {
    --compare-hover-color: #111;
  }

  .text-hover-product__item {
    margin-bottom: 12px;
  }

  .text-hover-product__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 15.625% 0 0;
  }

  .text-hover-product__label {
    margin-bottom: 8px;
    font-size: 13px;
    line-height: 161.5384615385%;
    font-weight: 600;
    opacity: 0;
    height: 0;
    visibility: hidden;
    transition: height 0.3s ease 0s;
  }

  .text-hover-product__sizes {
    display: flex;
    align-items: center;
  }

  .text-hover-product__size {
    padding: 6.5px 13px;
    font-size: 10px;
    line-height: 160%;
    background-color: #F8F8FD;
    border-radius: 4px;
    border: 1px solid transparent;
    cursor: pointer;
    transition: border-color 0.3s ease 0s;
  }

  .text-hover-product__size._active {
    border-color: var(--main-yellow);
  }

  .text-hover-product__size:not(:last-of-type) {
    margin: 0px 4px 0px 0px;
  }

  .text-hover-product__lot-sizes {
    margin-left: 12px;
    font-size: 13px;
    line-height: 161.5384615385%;
    font-weight: 600;
    color: var(--add-gray);
  }

  .text-hover-product__name {
    font-size: 13px;
    line-height: 161.5384615385%;
    color: var(--add-gray);
  }

  .text-hover-product__value {
    font-size: 13px;
    line-height: 161.5384615385%;
    color: var(--darkest-gray);
  }

  .text-hover-product__articol {
    opacity: 0;
    height: 0;
    visibility: hidden;
    transition: height 0.3s ease 0s;
    font-size: 13px;
    line-height: 161.5384615385%;
    font-weight: 600;
    color: var(--add-gray);
  }

  .about-us__link:hover {
    color: var(--hover-color);
  }
}