@use 'sass:math';

.product {
    position: relative;
    max-width: 320px;

    position: relative;
    display: flex;
    flex-direction: column;

    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    overflow: hidden;

    @media (any-hover: hover) {
        &-hover {
            padding-top: 250px;
            transition: padding-top 0.3s ease 0s;

            &:hover {
                padding-top: 70px;

                .text-hover-product__label,
                .text-hover-product__articol {
                    animation: showText .7s forwards;
                    height: auto;
                }
            }
        }

        &__main {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            flex: 1 1 auto;
            transition: flex-grow 0.3s ease .3s;
        }

        &__bottom {
            flex: 1 1 auto;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }
    }


    // .product__discount
    &__discount {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;

        display: block;
        width: 68px;
        height: 68px;
        padding: 14px 0 0 8px;

        color: var(--main-white);
        font-size: 19px;
        font-weight: 700;
        letter-spacing: math.div(.3px, 19px) * 1em;
        line-height: math.div(24px, 19px) * 100%;

        background-color: var(--discount-color);
        border-radius: 0 0 100% 0;

        @media (max-width:$md4+px) {
            width: 33px;
            height: 33px;
            padding: 5px 0 0 4px;
            font-size: 10px;
            font-weight: 600;
            line-height: math.div(16px, 10px) * 100%;
        }
    }

    // .product__favourites
    &__favourites {
        position: absolute;
        z-index: 2;
        top: 14px;
        right: 26px;

        @media (max-width:$md4+px) {
            top: 0;
            right: 0;
        }
    // .product__favorites-mobile
    &-mobile {
        display: none;
        @media (max-width:$md4+px){
              display: flex;
              justify-content:center;
              align-items: center;
              flex: 1 1 83%;
        }
    }
    //.product__favourites_comparison
    &_comparison {
        @media (max-width:$md4+px){
             display: none; 
        }
    }
    }

    // .product__image-wrapper
    &__image-wrapper {
        margin-bottom: 43px;
        padding: 26px 10px 0;

        @media (max-width:$md4+px) {
            margin-bottom: 13px;
            padding: 15px 2px;
        }

        @media (any-hover: hover) {
            &_hover {
                position: absolute;
                top: 0;
                width: 100%;
                height: auto;
                z-index: 1;

                transition: top 0.5s ease 0s;

                &._hide-image {
                    top: -300px;
                }
            }
        }
    }

    // .product__image
    &__image {
        width: 100%;
        max-width: 300px;
        padding: 0px 0px 60% 0px;
    }

    // .product__name
    &__name {
        margin-bottom: 10px;

        font-size: 16px;
        line-height: math.div(26px, 16px) * 100%;
        text-align: left;

        @media (max-width:$md3+px) {
            max-width: 115px;
            font-size: 13px;
        }

        @media (max-width: 335px) {
            font-size: 12px;
        }
    }

    // .product__year
    &__year {
        margin-bottom: 14px;

        font-size: 13px;
        line-height: 100%;
    }

    // .product__reviews
    &__reviews {
        margin-bottom: 15px;

        @media (max-width:$md4+px) {
            margin-bottom: 4px;
        }
    }

    // .product__colors
    &__colors {
        margin-bottom: 25px;

        @media (max-width:$md4+px) {
            display: none;
        }
    }

    // .product__prices
    &__prices {
        display: flex;
        align-items: flex-end;
        margin-bottom: 35px;

        letter-spacing: math.div(.3px, 22px) * 1em;
        line-height: math.div(28px, 22px) * 100%;

        @media (max-width:$md3+px) {
            flex: 1 1 auto;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 15px;
        }
    }

    // .product__price
    &__price {

        // .product__price_new
        &_new {
            margin-right: 20px;

            font-size: 22px;
            font-weight: 700;

            @media (max-width:$md3+px) {
                margin: 0;
            }

            @media (max-width:$md4+px) {
                font-size: 16px;
                font-weight: 600;
            }
        }

        // .product__price_old
        &_old {
            position: relative;
            flex: 0 1 84px;

            display: flex;
            justify-content: flex-end;

            color: var(--medium-gray);
            font-size: 19px;
            font-weight: 700;

            @media (max-width:$md3+px) {
                text-decoration: line-through;
                flex: 0 1 auto;
            }


            @media (max-width:$md4+px) {
                font-size: 10px;
                font-weight: 600;
            }

            &::before {
                content: '';
                width: 110%;
                height: 1.5px;
                position: absolute;
                top: 50%;
                left: 0;

                background-color: var(--medium-gray);
                transform: translate(0px, 50%);

                @media (max-width:$md3+px) {
                    display: none;
                }
            }
        }
    }

    // .product__bottom-row
    &__bottom-row {
        width: 100%;
        display: flex;
        align-items: center;
    }

    // .product__button
    &__button {
        position: relative;
        flex: 0 1 83.75%;
    }


    // .product__icon-cart
    &__icon-cart {}

    // .product__btn
    &__btn {
        flex: 1 0 auto;
        padding: 17px 10px;

        display: flex;
        justify-content: center;
        align-items: center;

        background-color: var(--main-white);

        cursor: pointer;

        transition: all 0.3s ease 0s;

        @media (any-hover: hover) {
            &:hover {
                background-color: var(--light-gray);

                svg {
                    --compare-hover-color: #111;
                }
            }
        }

        @media (max-width:$md4+px) {
            padding: 9.5px 15px;
        }
        &_remove {
            @media (max-width:$md4+px){
                flex: 1 1 83%;
                 padding: 0; 
            }
        }
    }

}

.reviews {

    // .reviews__empty
    &__empty {
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;
        color: var(--main-gray);
    }

    // .reviews__completed
    &__completed {}

    // .reviews__rating
    &__rating {
        font-size: 15px;
    }

    // .reviews__quantity
    &__quantity {
        color: var(--main-gray);
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;

        @media (max-width:$md4+px) {
            line-height: 100%;
            font-size: 10px;
        }
    }
}

.color-scheme-product {
    display: inline-flex;

    padding: 2px;

    border-radius: 3px;
    border: 1px solid transparent;
    cursor: pointer;

    transition: border-color 0.3s ease 0s;

    &._active {
        border-color: var(--main-yellow);
    }

    &__wrapper {
        position: relative;
        width: 32px;
        height: 19px;
        display: flex;
        justify-content: center;
        align-items: center;

        &_picking {
            width: 97px;
            height: 59px;

            @media (max-width:$md4+px) {
                width: 85px;
                height: 45px;
            }
        }

    }

    // .color-scheme-product__main
    &__main {
        width: 100%;
        height: 100%;

        background-color: #000;
        border-radius: 3px;

        clip-path: polygon(0 0, 67% 0, 41% 100%, 0% 100%);
    }

    // .color-scheme-product__second
    &__second {
        position: absolute;
        top: 0px;
        right: 0px;
        width: 100%;
        height: 100%;
        ;
        border-radius: 3px;


        clip-path: polygon(66% 0, 100% 0%, 100% 100%, 40% 100%);

        // .color-scheme-product__color_yellow
        &_yellow {
            background-color: var(--main-yellow);
        }

        // .color-scheme-product__color_white
        &_white {
            background-color: var(--dark-white);
        }
    }
}

.text-hover-product {
    display: none;

    @media (any-hover: hover) {
        // .text-hover-product__item
        &__item {
            margin-bottom: 12px;
        }

        // .text-hover-product__row
        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 5px 15.625% 0 0;
        }

        // .text-hover-product__label
        &__label {

            margin-bottom: 8px;

            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            font-weight: 600;


            opacity: 0;
            height: 0;
            visibility: hidden;

            transition: height 0.3s ease 0s;
        }

        // .text-hover-product__sizes
        &__sizes {
            display: flex;
            align-items: center;
        }

        // .text-hover-product__size
        &__size {
            padding: 6.5px 13px;

            font-size: 10px;
            line-height: math.div(16px, 10px) * 100%;

            background-color: #F8F8FD;
            border-radius: 4px;

            border: 1px solid transparent;
            cursor: pointer;
        
            transition: border-color 0.3s ease 0s;
        
            &._active {
                border-color: var(--main-yellow);
            }

            &:not(:last-of-type) {
                margin: 0px 4px 0px 0px;
            }
        }

        // .text-hover-product__lot-sizes
        &__lot-sizes {
            margin-left: 12px;

            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            font-weight: 600;
            color: var(--add-gray);
        }

        // .text-hover-product__name
        &__name {
            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            color: var(--add-gray);
        }

        // .text-hover-product__value
        &__value {
            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            color: var(--darkest-gray);
        }

        // .text-hover-product__articol
        &__articol {
            opacity: 0;
            height: 0;
            visibility: hidden;

            transition: height 0.3s ease 0s;
            font-size: 13px;
            line-height: math.div(21px, 13px) * 100%;
            font-weight: 600;
            color: var(--add-gray);
        }
    }

}