@use 'sass:math';

.hero {

    // .hero__container
    &__container {
    }
    // .hero__content
    &__content {
        position: relative;
    }
    // .hero__slider
    &__slider {
        margin-bottom: 28px;
        // display: flex;
    }
    // .hero__slide
    &__slide {
        padding: 0px 0px 43.4% 0px;
        @media (max-width:$md4+px){
             padding-bottom: 55.5%; 
        }
    }
    // .hero__controls
    &__controls {
        
    }
    // .hero__arrows
    &__arrows {
        @media (max-width:$md4+px){
            display: none; 
       }
    }
    // .hero__arrow
    &__arrow {
        position: absolute;
        top: 47%;
        transform: translate(0px,-50%);
        // .hero__arrow_left
        &_left {
            left: -23px;
        }
        // .hero__arrow_right
        &_right {
            right: -23px;
        }
    }
    // .hero__bullets
    &__bullets {
        display: flex;
        justify-content: center;

        span {
            &:not(:last-child){
                margin:0px 12px 0px 0px;
            }
        }
    }
}

.slide-hero {
    position: relative;
    // .slide-hero__image
    &__image {
    }
    // .slide-hero__text
    &__text {
        position: absolute;
        top: 0;
        left: 0;

        display:flex;
        flex-direction: column;
        justify-content:space-between;
        // width: math.div(544,1344) * 100%;
        min-width: 40.5%;
        max-width: 51%;
        height: 100%;

        padding-right: 23px;
        @include adaptiv-value("padding-top", 78, 15, 0);
        @include adaptiv-value("padding-bottom", 64, 19, 0);
        @include adaptiv-value("padding-left", 64, 16, 1);
        background-color: #3c3c3c;

        @media (max-width:$md2+px){
            padding-right: 15px;
        }
        @media (max-width: 350px){
              padding-right: 5px;
        }
    }
    // .slide-hero__label
    &__label {
        max-width: 445px;
        margin-bottom: 40px;
        color: var(--main-white);
        @include adaptiv-font(55,13,1);
        font-weight: 800;
        letter-spacing: math.div(.17px,55px) * 1em;
        line-height: math.div(66px,55px) * 100%;

        @media (max-width: 1390px){
            font-size: 46px;
      }

        @media (max-width:1375px){
            //  max-width: 330px; 
             @include adaptiv-font(46,13,1);
        }
        @media (max-width:$md2+px){
            max-width: 241px;
        }
        @media (max-width:$md3+px){
             max-width: 190px; 
        }
        @media (max-width:$md4+px){
             max-width: 107px; 
        }
    }
    // .slide-hero__description
    &__description {
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;

        color: var(--main-yellow);
    }
    // .slide-hero__cifres
    &__cifres {
        margin-right: 21px;
        margin: 0px 21px 21px 0px;
        line-height: 75%;

        @include adaptiv-font(206,49,1);
        font-weight: 900;
        letter-spacing: math.div(.62px,206px) * 1em;

        @media (max-width: 1390px){
              font-size: 160px;
        }

        @media (max-width:1375px){
             margin-bottom: 15px;
             @include adaptiv-font(160,49,1);
        }
        @media (max-width:$md3+px){
            margin-right: 4px; 
       }
        @media (max-width:$md4+px){
             margin-bottom: 8px; 
        }
    }
    // .slide-hero__word
    &__word {
        padding-bottom: 16px;

        @include adaptiv-font(34,8,1);
        font-weight: 700;
        letter-spacing: math.div(.3px,34px) * 1em;
        line-height: math.div(40px,34px) * 100%;

        @media (max-width:$md4+px){
             padding-bottom: 9px; 
        }
    }
}
