@use 'sass:math';

.top-sales {
    // .top-sales__container
    &__container {
    }
    // .top-sales__header
    &__header {
        display: flex;
        justify-content:space-between;
        align-items: flex-end;
        margin-bottom: 46px;

        @media (max-width:$md4+px){
            justify-content: center;
             margin-bottom: 24px; 
        }

    }
    // .top-sales__quantity
    &__quantity {

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md4+px){
             display: none; 
        }
    }
    // .top-sales__title
    &__title {
        margin-bottom: 0;
    }
    // .top-sales__show-all
    &__show-all {
        display: flex;
        align-items:center;

        @media (max-width:$md4+px){
            display: none; 
       }
    }
    // .top-sales__link-text
    &__link-text {
        margin-right: 17px;

        text-align: center;
    }
    // .top-sales__link-icon
    &__link-icon {
        transition: transform 0.3s ease 0s;
    }
    // .top-sales__content
    &__content {
        position: relative;
    }
    // .top-sales__products
    &__products {
        &._swiper {
            overflow: visible;

            .swiper-slide {
                opacity: .3;
    
                transition: opacity 0.3s ease 0s;
    
                &-visible {
                    opacity: 1;
                }
            }
        }
    }
    // .top-sales__controls
    &__controls {
    }
    // .top-sales__arrows
    &__arrows {
    }
    // .top-sales__arrow
    &__arrow {
        position: absolute;
        top: 50%;
        top: calc(50% - 80px);

        @media (max-width:$md4+px){
             display: none; 
        }
        // .top-sales__arrow_left
        &_left {
            left: 0px;
        }
        // .top-sales__arrow_right
        &_right {
            right: 0px;
        }
    }
}
