@use 'sass:math';

$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1375;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

.page {
    // .page__hero
    &__hero {
        margin-bottom: 62px;

        @media (max-width:$md4+px){
             margin-bottom: 20px; 
        }
    }
    // .page__categories
    &__categories {
        margin-bottom: 96px;
        
        @media (max-width:$md4+px){
             margin-bottom: 28px; 
        }
        &_single {
            margin-bottom: 144px;
            @media (max-width:$md4+px){
                 margin-bottom: 96px; 
            }
        }
    }
    // .page__top-sales
    &__top-sales {
        margin-bottom: 62px;
        @media (max-width:$md4+px){
             margin-bottom: 44px; 
        }
    }
    // .page__selections
    &__selections {
        margin-bottom: 96px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .page__partners
    &__partners {
        //+20px от лого идёт
        margin-bottom: 72px;

        @media (max-width:$md4+px){
             margin-bottom: 54px; 
        }
    }
    // .page__benefits
    &__benefits {
        margin-bottom: 98px;
        @media (max-width:$md4+px){
             margin-bottom: 48px; 
        }
    }
     // .page__about-us
     &__about-us {
         margin-bottom: 136px;
         @media (max-width:$md4+px){
              margin-bottom: 32px; 
         }
    }

    // .page__stocks
    &__stocks {
    }
}
.selections {
    &__title {
        margin: 0px 0px math.div(44px,34px) * 1em 0px; 
    }
}



@import "mixins";
@import "hero";
@import "categories";
@import "../product";
@import "top-sales";
@import "partners";
@import "about-us";
@import "stocks";

