@use 'sass:math';

.categories {
    // .categories__container
    &__container {
    }
    // .categories__title
    &__title {
        margin: 0px 0px math.div(32px,34px) * 1em 0px; 
    }
    // .categories__content
    &__content {
    }
    // .categories__items
    &__items {
        display: grid;
        grid-template-columns: repeat(12,1fr);
        @include adaptiv-value("grid-auto-rows", 279, 120, 1);
        grid-gap: 24px;

        @media (max-width:$md3+px){
             grid-gap: 8px; 
        }
        // @media (max-width:$md4+px){
        //     grid-auto-rows: 120px;
        // }
    }
    // .categories__item
    &__item {
        grid-column: auto / span 4;

        @media (max-width:$md2+px){
            grid-column: auto / span 6;
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(9n),
        &:nth-child(10n) {
            @media (min-width:$md2+px){
                grid-column: auto / span 6; 
            }

            @media (min-width:$md4+px) and (max-width:$md2+px){
                grid-column: auto / span 12;
            }
        }
        &:nth-child(2),
        &:nth-child(10n) {
            @media (min-width:$md4+px) and (max-width:$md2+px){
                 grid-row: 3 / 4; 
            }
        }
    }
}

.category {
    position: relative;
    overflow: hidden;
    padding: 20px;

    transition: border-radius 0.3s ease 0s;

    @media (any-hover: hover){
        &:hover{
            border-radius: 12px;

            .category__image img{
                transform: scale(1.1);
            }
            .category__text-hover{
                animation: showText .7s forwards;

                &._medium {
                    height: 120px;
                }

                &._large {
                    height: 150px;
                }
            }
            .category__label {
                transform: translate(0px,-8px);
            }
        }
    }

    // .category__image
    &__image {
        z-index: -1;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        &::after{
            content:'';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(1, 1, 3, 0.36);
        }

        img {
            object-fit: cover;
            width: 100%;
            height: 100%;

            transition: transform 0.3s ease 0s;
        }
    }

    // .category__description
     &__description {
        width: 100%;
        height: 100%;


        display:flex;
        flex-direction: column;
        justify-content:center;
        align-items: center;
     }

    // .category__label
    &__label {
        max-width: 300px;

        color: var(--main-white);
        @include adaptiv-font(28,13,1);
        font-weight: 700;
        letter-spacing: math.div(.3px,28px) * 1em;
        line-height: math.div(36px,28px) * 100%;

        text-align: center;

        transition: transform 0.5s ease 0s;
    }
    // .category__text-hover
    &__text-hover{
        max-width: 300px;
        line-height: math.div(26px,16px) * 100%;
        text-align: center;
        color: #ededed;

        opacity: 0;
        height: 0;
        visibility: hidden;

        transition: height 0.3s ease 0s;

        @media (max-width:$md2+px){
            display: none;
        }
    }

}
@keyframes showText {
    0% {
    }
    1% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
}





