@use 'sass:math';

.partners {
    // .partners__container
    &__container {
    }
    // .partners__title
    &__title {
        @media (max-width:$md4+px){
             margin-bottom: 31px; 
        }
    }
    // .partners__content
    &__content {
        display: flex;
        justify-content:space-evenly;
        align-items: center;
        flex-wrap: wrap;

        @media (max-width: 600px){
              flex-direction: column;
              justify-content:flex-start;
        }

    }
    // .partners__item
    &__item {
        flex: 0 1 math.div(185,1260) * 100%;
        padding: 20px;

        @media (max-width: 600px){
              flex: 0 1 auto;
              display: flex;
              justify-content:space-between;
              align-items:center;
              width: 100%;
              padding: 0;

              &:not(:last-child){
                margin:0px 0px 28px 0px;
            }
        }

        &::after{
            @media (max-width: 600px){
                content:'';
                display: block;
                width: 16px;
                height: 16px;
                background: url('../img/icons/arrow-partners.svg') 0 0 no-repeat;
            }
        }
    }
    // .partners__image
    &__image {
        display: flex;
        justify-content:center;
        align-items: center;

        @media (max-width:600px){
              justify-content: flex-start;
        }

    }
}



