@use 'sass:math';

.stocks {
    padding: 52px 0px 64px;
    color:  var(--main-white);
    background-color: var(--darkest-gray);

    @media (max-width:$md4+px){
        padding: 32px 0;
    }
    // .stocks__container
    &__container {
    }
    // .stocks__head
    &__head {
        display: flex;
        justify-content:space-between;
        align-items:flex-end;
        margin-bottom: 43px;

        @media (max-width: 550px){
            justify-content: center;
        }

        @media (max-width:$md4+px){
            margin-bottom: 24px;
             padding-top: 0; 

            //  background-color: var(--main-white);
        }
    }
    // .stocks__body
    &__body {
        position: relative;
    }
   

    // .stocks__quantity
    &__quantity {
        padding-bottom: 1px;

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:550px){
             display: none; 
        }
    }
    // .stocks__title
    &__title {
        margin-bottom: 0;
    }
    // .stocks__show-all
    &__show-all {
        display: flex;
        align-items:center;
        padding: 0px 14px 1px 0px;

        @media (max-width:550px){
             display: none; 
        }
    }
    // .stocks__link-text
    &__link-text {
        margin-right: 17px;
    }
    // .stocks__link-icon
    &__link-icon {
    }
    // .stocks__items
    &__items {
        &._swiper {
            overflow: visible;

            .swiper-slide {
                opacity: .3;
    
                transition: opacity 0.3s ease 0s;
    
                &-visible {
                    opacity: 1;
                }
            }
        }
    }
    // .stocks__item
    &__item {
    }
    // .stocks__controls
    &__controls {
    }
    // .stocks__arrows
    &__arrows {
    }
    // .stocks__arrow
    &__arrow {
        position: absolute;
        top: 45.8%;
    
        @media (max-width:$md4+px){
             display: none; 
        }
        &_left {
            left: -24px;
        }
        &_right {
            right: -24px;
        }
    }
}

.item-stocks {
    position: relative;
    display:flex;
    flex-direction: column;
    align-items: center;

    padding: 48px 32px 23px;

    background-color: var(--darken-gray);
    border-radius: 8px;

    @media (max-width:$md4+px){
         padding: 24px 16px 16px; 
    }
    // .item-stocks__period
    &__period {
        position: absolute;
        top: 0;
        left: 0;
        width: 105px;
        height: 42px;

        display:flex;
        justify-content:center;
        align-items: center;

        font-weight: 800;
        color: var(--hover-color);
        letter-spacing: math.div(.3px,16px) * 1em;
        line-height: math.div(26px,16px) * 100%;

        background-color: var(--add-yellow);
        border-radius: 4px 0 4px 0;

        @media (max-width:$md4+px){
             top: 8px;
             left: 8px;
             width: 64px;
             height: 24px;

             font-size: 10px;
             
             color: var(--darken-gray);
             border-radius: 4px;
        }
    }
    // .item-stocks__icon
    &__icon {
        margin-bottom: 24px;

        @media (max-width:$md4+px){
             margin-bottom: 18px;
             
             svg {
                 width: 48px;
                 height: 18px;
             }
        }
    }
    // .item-stocks__label
    &__label {
        max-width: 258px;
        padding-bottom: 32px;

        font-size: 22px;
        font-weight: 700;
        letter-spacing: math.div(.3px,22px) * 1em;
        line-height: math.div(28px,22px) * 100%;
        text-align: center;

        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        @media (max-width:$md4+px){
             padding-bottom: 17px; 

             font-size: 16px;
        }

    }
    // .item-stocks__description
    &__description {
        flex: 1 1 auto;
        max-width: 260px;
        margin-bottom: 62px;
        padding-top: 24px;

        text-align: center;
        opacity: .75;
        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md4+px){
             padding-top: 16px; 

             font-size: 10px;
        }
    }
    // .item-stocks__show-more
    &__show-more {
    }
}





